body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: flex-end; }
  nav .menu {
    margin-right: 1rem;
    display: flex;
    flex: 1 1;
    justify-content: flex-end; }
    nav .menu__item {
      padding: 0.25rem 0;
      margin: 0 1rem;
      font-size: 1.25rem;
      font-weight: 400;
      border-bottom: 2px solid transparent; }
      nav .menu__item:hover {
        cursor: pointer; }

.landing-page {
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center; }
  .landing-page main {
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    justify-content: center;
    padding: 3.25rem 1.5rem 1rem;
    text-align: center; }
    .landing-page main .intro-wrapper {
      flex: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center; }
      .landing-page main .intro-wrapper > .intro-name {
        font-family: 'Reem Kufi', sans-serif;
        font-size: 4rem;
        font-weight: bold;
        line-height: 4rem;
        padding-bottom: 1rem; }
      .landing-page main .intro-wrapper > .tagline {
        font-size: 1.375rem;
        margin: 1.5rem 0;
        font-weight: 300; }
    .landing-page main .browser-notes {
      font-size: 0.75rem; }
      .landing-page main .browser-notes span {
        border-width: 1px;
        border-style: solid;
        padding: 0.125rem 0.5rem; }

.about-page {
  padding-top: 2rem;
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center; }
  .about-page h1 {
    padding: 1rem 0;
    font-size: 3rem; }
  .about-page .content-grid {
    flex: auto; }
  .about-page .about-wrapper .about-content {
    font-size: 0.875rem;
    line-height: 1.5rem; }
    .about-page .about-wrapper .about-content p {
      font-weight: 300;
      padding: 0.5rem 0; }
    .about-page .about-wrapper .about-content .text-emoji {
      padding: 2rem 0;
      font-size: 1.5rem;
      font-weight: 300;
      text-align: center; }

